import React from "react";
import { Helmet } from "react-helmet";
import { Link } from "gatsby";

import "../styles/index.scss";

const handleClick = (e) => {
    let mobileMenu = document.getElementById("mobile-menu");
    if (mobileMenu.className.indexOf("w3-show") === -1) {
        mobileMenu.className += " w3-show";
    } else {
        mobileMenu.className = mobileMenu.className.replace(" w3-show", "");
    }
}

export default () => (
    <header>
        <Helmet>
            <title>Antony Ndungu</title>
            <link rel="stylesheet" href="https://www.w3schools.com/w3css/4/w3.css" />
            <link rel="stylesheet" href="https://www.w3schools.com/lib/w3-theme-teal.css" />
            <link rel="stylesheet" href="https://fonts.googleapis.com/css?family=Roboto" />
            <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css" />
        </Helmet>
        <div className="">
            <div className="w3-bar w3-xlarge w3-light-grey" >
                <Link to="/" className="w3-bar-item w3-button w3-hover-none w3-border-white w3-bottombar w3-hover-border-theme w3-hover-text-theme" activeClassName="w3-theme" style={{ width: "25%" }}>Home</Link>
                <Link to="/blog" className="w3-bar-item w3-button w3-hover-none w3-border-white w3-bottombar w3-mobile w3-hover-border-theme w3-hover-text-theme w3-hide-small" activeClassName="w3-theme" style={{ width: "25%" }}>Blog</Link>
                <Link to="/about" className="w3-bar-item w3-button w3-hover-none w3-border-white w3-bottombar w3-mobile w3-hover-border-theme w3-hover-text-theme w3-hide-small" activeClassName="w3-theme" style={{ width: "25%" }}>About</Link>
                <Link to="/contact" className="w3-bar-item w3-button w3-hover-none w3-border-white w3-bottombar w3-mobile w3-hover-border-theme w3-hover-text-theme w3-hide-small" activeClassName="w3-theme" style={{ width: "25%" }}>Contact</Link>
                <button className="w3-bar-item w3-button w3-right w3-hide-large w3-hide-medium" onClick={handleClick}>&#9776;</button>
            </div>
        </div>
        <div id="mobile-menu" className="w3-bar-block w3-xlarge w3-light-grey w3-hide w3-hide-large w3-hide-medium">
            <Link to="/blog" className="w3-bar-item w3-button w3-hover-none w3-border-white w3-bottombar w3-hover-border-theme w3-hover-text-theme" activeClassName="w3-theme">Blog</Link>
            <Link to="/about" className="w3-bar-item w3-button w3-hover-none w3-border-white w3-bottombar w3-hover-border-theme w3-hover-text-theme" activeClassName="w3-theme">About</Link>
            <Link to="/contact" className="w3-bar-item w3-button w3-hover-none w3-border-white w3-bottombar w3-hover-border-theme w3-hover-text-theme" activeClassName="w3-theme">Contact</Link>
        </div>
    </header>
);